export default {
	name: "ygc-comment",
	data() {
		return {
			inputValue: '',
			boardHeight: 328
		}
	},
	props:{
		placeholder: {
			type: String,
			required: true
		},
		focus: {
			type:Boolean,
			required: true
		}
	},
	methods: {
		getKeyHeight(e){
			this.boardHeight = e.detail.height
			console.log('FMousekeyBoard',e.detail.height)
		},
		blur: function() {//失焦触发通知父组件
			var that = this;
			setTimeout(function(){
				that.$emit('blur')
			},100);
		},
		startRecognize: function () {
			var options = {};
			var that = this;
			options.engine = 'iFly';
			that.inputValue = "";
			plus.speech.startRecognize(options, function (s) {
				console.log(s);
				that.inputValue += s;
			}, function (e) {
				console.log("语音识别失败：" + e.message);
			});
		},
		sendMessge: function () {
			if (!this.inputValue) {
				uni.showModal({
					content:"还没有输入内容哦!",
					showCancel:false
				})
				return;
			}
			var that = this;
			//点击发送按钮时，通知父组件用户输入的内容
			this.$emit('send-message', {
				type: 'text',
				content: that.inputValue
			});
			that.inputValue = '';//清空上次输入的内容
		}
	}
}
